(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('ResearchItemController', itemControllerFunc);

  /** @ngInject */
  function itemControllerFunc($scope, $log, NgTableParams, researchItemService, modalService, toastr, security) {
      var vm = this;

      vm.saveGroup = saveGroup;
      vm.edit = edit;
      vm.deleteGroup = deleteGroup;
      vm.formData = {};
      vm.add = add;
      vm.deptCode = security.getFromSession().role.deptCode;
      vm.open = openEdit;

      initData();

      function initData() {
          researchItemService.getResearchItems().then(function (response){
            if(response){
              vm.lst = response.data.data.lst;
              vm.tableParams = new NgTableParams({}, {
                dataset: vm.lst
              });
            }
        });
      }

      function edit(row) {
        vm.formData = angular.extend({}, row);
        vm.selectedLevel = row;

        if (row) {
          if (row.beds) {
            vm.formData.bedList = row.beds.split(';');
          }
        }
      }

      function add() {
        vm.formData = {};
      }

      /************
      ********
      ************/
      vm.removeFlow = removeFlow;
      vm.addFlow = addFlow;

      function addFlow() {
        if (vm.flowText === '') {
          return;
        }

        vm.formData.bedList = vm.formData.bedList || [];

        vm.formData.bedList.push(vm.flowText);

        //  ng-model
        vm.flowText = '';
      }

      function removeFlow($index) {
        vm.formData.bedList.splice($index, 1);
      }

      function saveGroup() {
        // debugger;
        var data = angular.extend({}, vm.formData);

        data.beds = data.bedList ? data.bedList.join(';') : '';
        data.deptCode = vm.deptCode;
        // return;
        bedService.saveGroup(data).then(function(response) {
          if (response && response.data.code === '0') {
            toastr.success('', '');
            initData();
            vm.formData = {};
            modalService.close('success');
          }
        });
      }

      function deleteGroup(seqId) {
        bedService.deleteGroup(seqId).then(function (response) {
          if (response && response.data.code === '0') {
            toastr.success('', '');
            initData();
            //
            if(seqId == vm.formData.seqId){
                vm.formData = {};
            }
            modalService.close('success');
          }
        });
      }
        //
      function openEdit() {
          modalService.open({
              templateUrl: 'app/manual/item/item.model.html',
              size: 'lg',
              ok: saveGroup,
              data: {
                  deptCode: vm.deptCode
              },
              methodsObj: {
                  eventTimeCalender: function () {
                      var m_this = this;

                      m_this.eventTimeOpen = true;
                  },

                  setUser: function ($index) {
                      var m_this = this;

                      m_this.formData.flowList.splice($index, 1);
                  }
              }
          });
      }
  }



})();

