(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('formService', formServiceFunc);

  /** @ngInject */
  function formServiceFunc($window, apiHost, $http, $log, $q, toastr, security) {
    var service = {
        getFormList: getFormList,
        saveForm : saveForm,
        deleteForm : deleteForm
    };

    return service;

    /**
     * [del ]
     * @param  {[string]} seqId []
     * @return {[object]}       [promise]
     */
    function deleteForm(seqId) {
      return $http.post(apiHost.qualityForm.deleteQualityForm, {
          seqId: seqId
        })
        .then(complete);
    }

    /**
     * [save ]
     * @param  {[object]} dataObj [seqId: number; damageLevelName: string; flow: string]
     * @return {[object]}         [promise]
     */
    function saveForm(dataObj) {
      return $http.post(apiHost.qualityForm.saveQualityForm, dataObj)
        .then(complete);
    }

    /**
     * [get ]
     * @param  {[object]} params []
     * @return {[object]}        [promise]
     */
    function getFormList(deptCode,type) {
        var param = {};

      param.deptCode = security.getFromSession().role.deptCode;

      return $http.get(apiHost.qualityForm.getFormList, {
          params: param
      })
        .then(complete);
    }

    function complete(response) {
        if (response.data.code === '0') {
            return response;
        }
    }
  }
})();

