(function (){
    'use strict';

    angular
        .module('nqms')
        .controller('MalCaseUpLevelController',levelControllerFun);

    function levelControllerFun($scope, $log, NgTableParams, $filter, levelService, modalService, toastr){
        var vm = this;

        vm.save = save;
        vm.edit = edit;
        vm.del = del;
        vm.formData = {};
        vm.add = add;

        initData();

        // 
        function initData() {
          levelService.get().then(function(response) {
            if (response) {
              vm.lst = response.data.data.lst;
              // debugger;
              vm.tableParams = new NgTableParams({}, {
                dataset: vm.lst
              });
            }
          });
        }

        function edit(row) {
          // 
          vm.formData = angular.extend({}, row);
          vm.selectedLevel = row;

          if (row) {
            if (row.flow) {
              vm.formData.flowList = row.flow.split('|');
            }
          }
        }

        function add() {
          vm.formData = {};
        }

        /************
        ********
        ************/
        vm.removeFlow = removeFlow;
        vm.addFlow = addFlow;

        function addFlow() {
          if (vm.flowText === '') {
            return;
          }

          vm.formData.flowList = vm.formData.flowList || [];

          vm.formData.flowList.push(vm.flowText);

          //  ng-model
          vm.flowText = '';
        }

        function removeFlow($index) {
          vm.formData.flowList.splice($index, 1);
        }

        function save() {
          // debugger;
          var data = angular.extend({}, vm.formData);

          data.flow = data.flowList ? data.flowList.join('|') : '';
          // return;
          levelService.save(data).then(function(response) {
            if (response && response.data.code === '0') {
              toastr.success('', '');
              initData();
              vm.formData = {};
              modalService.close('success');
            }
          });
        }

        function del(seqId) {
          levelService.del(seqId).then(function (response) {
            if (response && response.data.code === '0') {
              toastr.success('', '');
              initData();
              //
              if(seqId == vm.formData.seqId){
                  vm.formData = {};
              }
              modalService.close('success');
            }
          });
        }
    }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('handleService', handleServiceFunc);

  /** @ngInject */
  function handleServiceFunc($window, apiHost, $http, $log, $q, toastr) {
    var service = {
      saveReportDetail: saveReportDetail,
      saveMeasure: saveMeasure,
      saveRequirement: saveRequirement,
      saveEstimate: saveEstimate,
      deleteMeasure: deleteMeasure,
      deleteRequirement: deleteRequirement,
      deleteEstimate: deleteEstimate,
      getReportDetail: getReportDetail,
      getEventMeasures: getEventMeasures,
      getEventRequirement: getEventRequirement,
      del: del
    };

    return service;

    /**
     * [del ]
     * @param  {[string]} seqId []
     * @param  {[string]} reportCode []
     * @return {[object]}       [promise]
     */
    function del(seqId, reportCode) {
      return $http.post(apiHost.events.deleteEventReport, {
          seqId: seqId,
          reportCode: reportCode
        })
        .then(complete);
    }

    function getReportDetail(params) {
      return $http.get(apiHost.events.getReportDetail, {
        params: params
      })
        .then(complete);
    }

    function getEventMeasures(params) {
      return $http.get(apiHost.events.getEventMeasures, {
        params: params
      })
        .then(complete);
    }

    function getEventRequirement(params) {
      return $http.get(apiHost.events.getEventRequirement, {
        params: params
      })
        .then(complete);
    }

    /**
     * [save ]
     * @param  {[object]} dataObj
     * @return {[object]}         [promise]
     */
    function saveReportDetail(dataObj) {
      return $http.post(apiHost.events.saveReportDetail, dataObj)
        .then(complete);
    }

    function saveMeasure(dataObj) {
      return $http.post(apiHost.events.saveEventMeasures, dataObj)
        .then(complete);
    }

    function saveRequirement(dataObj) {
      return $http.post(apiHost.events.saveEventRequirement, dataObj)
        .then(complete);
    }

    // TODO 
    function saveEstimate(dataObj) {
      return $http.post(apiHost.events.saveReportDetail, dataObj)
        .then(complete);
    }

    /**
     * [get ]
     * @param  {[object]} params []
     * @return {[object]}        [promise]
     */
    function get(params) {
      return $http.get(apiHost.events.getEventLevels, {
        params: params
      })
        .then(complete);
    }

    function complete(response) {
      if (response.data.code === '0') {
        return response;
      }
    }


    function deleteRequirement(params) {
      return $http.post(apiHost.events.deleteEventRequirement, params)
        .then(complete);
    }

    function deleteMeasure(params) {
      return $http.post(apiHost.events.deleteEventMeasures, params)
        .then(complete);
    }

    function deleteEstimate(params) {
      return $http.post(apiHost.events.deleteEventReport, params)
        .then(complete);
    }

  }
})();

(function() {
  'use strict';
  angular
    .module('nqms')
    .controller('MalCaseUpHandleController', handleControllerFun);

  function handleControllerFun($rootScope, $scope, $log, $q, NgTableParams, $filter, security, levelService, modalService, toastr, fixService, deptService, handleService, filesService) {
    var vm = this;
    var formData = {};
    var metaData = {};

    vm.rootScope = $rootScope;
    vm.scope = $scope;
    vm.open = open;
    vm.del = del;

    initData();

    // 
    function initData() {
      fixService.getEventList().then(function(response) {
        if (response) {
          vm.lst = response.data.data.lst;
          // debugger;
          vm.tableParams = new NgTableParams({}, {
            dataset: vm.lst
          });
        }
      });
    }

    // TODO   >
    function open(row) {
      // 
      // formData = angular.extend({}, row);
      vm.selectedEvent = row;
      initModalData(row).then(open_it);
    }

    function open_it() {
      // debugger;
      modalService.open({
        templateUrl: 'app/malCaseUp/handle/handle.modal.html',
        size: 'lg',
        appendTo: angular.element('.handle-wrapper'),
        data: {
          formData: formData,
          metaData: metaData
        },
        methodsObj: {
          saveReportDetail: saveReportDetail,
          // TODO 
          saveListItem: function(listName, itemData) {
            var m_this = this;
            var list_name = m_this.upFirstLetter(listName);
            var data_temp = {};

            list_name = list_name === 'Estimate' ? 'Measure' : list_name;

            data_temp.userCode = security.getFromSession().user.userCode;

            switch (listName) {
              case 'requirement':
                angular.extend(data_temp, {
                  reportCode: m_this.formData.reportCode,
                  seqId: m_this.formData[listName].seqId || null,
                  reqCode: m_this.formData[listName] && m_this.formData[listName].reqCode || null,
                  reqContent: m_this.formData[listName].reqContent,
                  remark: ''
                });
                break;
              case 'measure':
                if (!m_this.formData[listName].meaTime) {
                  data_temp.meaTime = moment().format('YYYY-MM-DD HH:mm:ss');
                }
                angular.extend(data_temp, {
                  seqId: m_this.formData[listName].seqId || null,
                  reportCode: m_this.formData.reportCode,
                  meaCode: m_this.formData[listName] && m_this.formData[listName].meaCode || null,
                  meaContent: m_this.formData[listName].meaContent,
                  headUserCode: m_this.formData[listName].headUserCode
                    // headUserName: m_this.formData[listName].headUserName
                });
                break;
              case 'estimate':
                angular.extend(data_temp, {
                  seqId: m_this.formData.measure.seqId || null,
                  reportCode: m_this.formData.reportCode,
                  meaCode: m_this.formData.measure && m_this.formData.measure.meaCode || null,
                  headUserCode: m_this.formData.measure.headUserCode,
                  meaContent: m_this.formData.measure.meaContent,
                  // headUserName: m_this.formData[listName].headUserName,
                  resStatus: '',
                  resDescribe: m_this.formData.measure.resDescribe
                });
                break;
              default:
                break;
            }

            if (!m_this.formData[listName + 'List']) {
              m_this.formData[listName + 'List'] = [];
            }

            // return;

            // 
            handleService['save' + list_name](data_temp).then(function(response) {
              if (response) {
                toastr.success('', '');
                // m_this.formData[listName].push(data_temp);
                m_this.rootScope.$broadcast('refresh' + list_name, m_this.formData.reportCode, m_this);
                m_this.formData[listName] = {};
              }
            });
          },
          removeListItem: function(params) {
            var m_this = this;
            var list_name = m_this.upFirstLetter(params.listName);
            var _params;

            m_this.formData[params.listName + 'List'].splice(params.index, 1);

            switch (params.listName) {
              case 'measure':
                _params = {
                  reportCode: m_this.reportCode,
                  meaCode: params.meaCode
                };
                break;
              case 'requirement':
                _params = {
                  seqId: params.seqId
                };
                break;
              case 'estimate':
                return;
              default:
                break;
            }

            handleService['delete' + list_name](_params).then(function(response) {
              if (response) {
                toastr.success('', '');
              }
            });
          },
          fillItem: function(itemName, item) {
            var m_this = this;

            m_this.formData[itemName] = angular.extend({}, item);
          },
          resetForm: function(itemName) {
            this.formData[itemName] = {};
          },
          upFirstLetter: function(str) {
            return str.replace(/^\w{1}/, function(match) {
              return match.toUpperCase();
            });
          }
        }
      });
    }

    function initModalData(row) {
      var reportCode = row.reportCode;
      var deptCode = security.getFromSession().role.deptCode;

      formData.reportCode = reportCode;

      // 

      var getUserList = filesService.getUserList(deptCode);

      var getEventRequirement = handleService.getEventRequirement({
        reportCode: reportCode
      });
      var getEventMeasures = handleService.getEventMeasures({
        reportCode: reportCode
      });
      var getReportDetails = handleService.getReportDetail({
        reportCode: reportCode
      });
      // var getEventRequirement = handleService.getEventRequirement({reportCode: reportCode});
      var _promise = $q.all([getEventRequirement, getEventMeasures, getReportDetails, getUserList]).then(function(response) {
        var requirements = response[0].data.data.lst;
        var measures = response[1].data.data.lst;
        var reportDetails = {};
        var userList = response[3];

        // debugger

        response[2].data.data.lst.forEach(function(item) {
          reportDetails[item.itemCode] = {
            value: item.itemValue,
            seqId: item.seqId
          };
        });

        // 
        metaData.malCaseInfo = row;
        metaData.userList = userList;
        formData.requirementList = requirements;
        formData.measureList = measures;
        formData.reportDetailList = reportDetails;
        // return response;
      });

      return _promise;
    }

    function removeFlow($index) {
      vm.formData.flowList.splice($index, 1);
    }

    function saveReportDetail() {
      var m_this = this;
      var data = angular.extend({}, m_this.formData.reportDetailList);
      var data_arr = [];
      var prop;
      debugger;
      // 
      for (prop in data) {
        if (data.hasOwnProperty(prop)) {
          data_arr.push({
            reportCode: m_this.formData.reportCode,
            itemCode: prop,
            seqId: data[prop].seqId || null,
            itemValue: data[prop].value
          });
        }
      }


      // return;
      handleService.saveReportDetail(data_arr).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          vm.scope.$broadcast('resfreshReportDetail', m_this.formData.reportCode, m_this);
          modalService.close('success');
        }
      });
    }

    function saveMeasure() {
      // debugger;
      var m_this = this;
      var data = angular.extend({}, m_this.formData.measures);
      // return;
      handleService.saveMeasure(data).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          modalService.close('success');
        }
      });
    }

    function saveRequirement() {
      var m_this = this;
      var data = angular.extend({}, m_this.formData.requirements);
      // return;
      handleService.saveRequirement(data).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          modalService.close('success');
        }
      });
    }

    function del(seqId, reportCode) {
      levelService.del(seqId, reportCode).then(function(response) {
        if (response && response.data.code === '0') {
          toastr.success('', '');
          initData();
          modalService.close('success');
        }
      });
    }

    function getEventRequirement(event, reportCode, context) {
      handleService.getEventRequirement({
        reportCode: reportCode
      }).then(function(response) {
        if (response) {
          context.formData.requirementList = response.data.data.lst;
          // vm.rootScope.$digest();
        }
      });
    }

    function getEventMeasures(event, reportCode, context) {
      handleService.getEventMeasures({
        reportCode: reportCode
      }).then(function(response) {
        if (response) {
          context.formData.measureList = response.data.data.lst;
          // vm.rootScope.$digest();
          // debugger;
        }
      });
    }

    function getReportDetail(event, reportCode, context) {
      handleService.getReportDetail({
        reportCode: reportCode
      }).then(function(response) {
        if (response) {
          context.formData.reportDetailList = response.data.data.lst;
          // vm.rootScope.$digest();
          // debugger;
        }
      });
    }

    // 
    vm.scope.$on('refreshMeasure', getEventMeasures);
    vm.scope.$on('refreshRequirement', getEventRequirement);
    vm.scope.$on('refreshReportDetail', getReportDetail);
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('fixService', fixServiceFunc);

  /** @ngInject */
  function fixServiceFunc($window, apiHost, $http, $log, $q, toastr, security) {
    var service = {
      save: save,
      getEventList: getEventList,
      del: del
    };

    return service;

    /**
     * [del ]
     * @param  {[string]} seqId []
     * @return {[object]}       [promise]
     */
    function del(seqId) {
      return $http.post(apiHost.events.deleteEventLevel, {
          seqId: seqId
        })
        .then(complete);
    }

    /**
     * [save ]
     * @param  {[object]} dataObj [seqId: number; damageLevelName: string; flow: string]
     * @return {[object]}         [promise]
     */
    function save(dataObj) {
      return $http.post(apiHost.events.saveEventLevel, dataObj)
        .then(complete);
    }

    /**
     * [get ]
     * @param  {[object]} params []
     * @return {[object]}        [promise]
     */
    function getEventList(params) {
      var param = params || {};

      param.userCode = security.getFromSession().user.userCode;
      param.deptCode = security.getFromSession().role.deptCode;

      return $http.get(apiHost.events.getEventList, {
        params: param
      })
        .then(complete);
    }

    function complete(response) {
      if (response.data.code === '0') {
        return response;
      }
    }
  }
})();


