(function (){
    'use strict';

    angular
        .module('nqms')
        .controller('MalCaseUpCallController',callControllerFun);

    function callControllerFun($scope, $log, NgTableParams, $filter, security, moment, levelService, modalService, toastr, fixService, callService, deptService,typeService){
        var vm = this;
        var formData = {};
        var metaData = {};

        vm.save = save;
        vm.open = open;
        vm.del = del;
        vm.add = add;

        // mock
        metaData.eventTypeList = [
          {
            code: 1,
            name: ''
          }, {
            code: 2,
            name: ''
          }, {
            code: 3,
            name: ''
          }, {
            code: 4,
            name: ''
          }, {
            code: 5,
            name: ''
          }
        ];

        initData();

        // 
        function initData() {
          fixService.getEventList().then(function(response) {
            if (response) {
              vm.lst = response.data.data.lst;
              // debugger;
              vm.tableParams = new NgTableParams({}, {
                dataset: vm.lst
              });
            }
          });
          typeService.getEventType().then(function(response){
              if (response) {
                  metaData.eventTypeList = response.data.data.lst;
              }
          });
        }

        // TODO   >
        function open(row) {
          // 
          formData = angular.extend({}, row);
          vm.selectedLevel = row;

          metaData.deptList = deptService.deptList && deptService.deptList.belongedDepts;

          // 
          // TODO 
          initLevelData().then(function () {
            if (!metaData.deptList) {
              deptService.getDeptData().then(function(response) {
                var response_data = response;

                if (response_data) {
                  metaData.deptList = response_data.belongedDepts;

                  if (metaData.deptList.length === 0) {
                    delete response_data.belongedDepts;
                    metaData.deptList = [response_data];
                  }

                  open_it();
                }
              });
            } else {
              open_it();
            }
          });

          // debugger;
          // 
            if(row){
            formData.eventDate = new Date(row.eventTime);
            formData.eventTime = new Date(row.eventTime);
                // 
                formData.level = {
                    damageLevel: row.damageLevel
                };
                // 
                formData.dept = {
                    deptCode: row.deptCode,
                    deptName: row.deptName
                };

                // 
                formData.eventType = {
                    eventTypeCode: row.eventTypeCode,
                    eventTypeName: row.eventTypeName
                };
            }


          // 
          formData.userCode = security.getFromSession().user.userCode;
          formData.userCode = security.getFromSession().user.userName;

          function open_it() {
            modalService.open({
              templateUrl: 'app/malCaseUp/call/call.modal.html',
              size: 'lg',
              ok: save,
              data: {
                formData: formData,
                metaData: metaData
              },
              methodsObj: {
                eventTimeCalender: function () {
                  var m_this = this;

                  m_this.eventTimeOpen = true;
                },

                setUser: function ($index) {
                  var m_this = this;

                  m_this.formData.flowList.splice($index, 1);
                }
              }
            });
          }
        }

        function initLevelData() {
          return levelService.get().then(function(response) {
            if (response) {
              metaData.levelList = response.data.data.lst;
            }
          });
        }

        function add() {
          vm.formData = {};
        }

        /************
        ********
        ************/
        vm.removeFlow = removeFlow;
        vm.addFlow = addFlow;

        function addFlow() {
          if (vm.flowText === '') {
            return;
          }

          vm.formData.flowList = vm.formData.flowList || [];

          vm.formData.flowList.push(vm.flowText);

          //  ng-model
          vm.flowText = '';
        }

        function removeFlow($index) {
          vm.formData.flowList.splice($index, 1);
        }

        function save(dataObj) {
          // debugger;
          var data = angular.extend({}, dataObj);
          var date_temp;
          var time_temp;
          // 
          data.deptName = data.dept.deptName;
          // 
          date_temp = moment(data.eventDate).format('YYYY-MM-DD');
          time_temp = moment(data.eventTime).format('HH:mm:ss');
          delete data.eventDate;
          delete data.dept;

          // code
          data.eventTypeCode = angular.fromJson(data.eventType).eventTypeCode;
          data.eventTypeName = angular.fromJson(data.eventType).eventTypeName;
          delete data.eventType;
          delete data.level;

          data.eventTime = moment(date_temp + ' ' + time_temp).format('YYYY-MM-DD HH:mm:ss');
          // debugger;
          // return;
          callService.save(data).then(function(response) {
            if (response && response.data.code === '0') {
              toastr.success('', '');
              initData();
              vm.formData = {};
              modalService.close('success');
            }
          });
        }

        function del(seqId, reportCode) {
          callService.del(seqId, reportCode).then(function (response) {
            if (response && response.data.code === '0') {
              toastr.success('', '');
              initData();
              modalService.close('success');
            }
          });
        }
    }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('acmeBread', acmeBreadFunc);

  /** @ngInject */
  function acmeBreadFunc($http, $log) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/breadcrumb/breadcrumb.html',
      scope: {},
      replace: true,
      controller: breadController,
      controllerAs: 'bc',
      link: loadingLinkFunc,
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function breadController($scope, $interval, moment, $window, $log, $location, $state, security) {
      var bc = this;
      bc.scope = $scope;

      bc.isActive = $scope.isActive;

      bc.tabTemp = security.currentUser.tab;

      /*
      
       */
      bc.removeTab = function($event) {
        // $log.info($event.currentTarget);
        var id = angular.element($event.currentTarget).data('id');
        var prev_li = angular.element($event.currentTarget).parents('li').prev('li');
        var next_li = angular.element($event.currentTarget).parents('li').next('li');
        var state_id;

        // 
        if (prev_li.length > 0) {
          state_id = '.' + prev_li.find('a').data('id');
        } else {
          if (next_li.length > 0) {
            state_id = '.' + next_li.find('a').data('id');
          } else {
            // 
            state_id = '';
          }
        }

        // 
        if (angular.element($event.currentTarget).parents('.tab-bread__item').hasClass('active')) {
          $state.go('home' + state_id);
        }

        security.removeFromBreadcrumb(id);
      };


      angular.element(document).ready(function() {
        var tabItem = angular.element('.tab-bread li');
        var tabItemLen = tabItem.length;
        bc.tabItemLen = tabItemLen;
      });

      /*
      
       */
      bc.isActive = function(path) {
        var pathLen = path.length;

        return path === $location.path().substr(-pathLen, pathLen);
      };

      $log.info('tab:', bc.tab);

      // "vm.creation" is avaible by directive option "bindToController: true"
    }

    function loadingLinkFunc(scope, elem, attrs) {
        scope.tabItemLen = function() {
            var tabItems = elem.find('li');
            var tabItemLen = tabItems.length;

            return tabItemLen;
        };

        scope.isLoading = function() {
          return $http.pendingRequests.length > 0;
        };

        scope.$watchGroup([scope.tabItemLen, scope.isLoading], function (resArr) {
            var tabWrapperWidth = elem.outerWidth();
            // return;
            if (!resArr[1]) {
                var tabItems = elem.find('li');
                var totalWidth = 0;
                var itemWidth = 0;
                if (resArr[0] === 0) return;

                tabItems.each(function(i, item) {
                    var _item = angular.element(item);
                    var _div = _item.find('.tab-bread__item');
                    var _span = _item.find('span');
                    var innerTextWidth = _span.innerWidth();
                    var tabOuterwidth = _item.outerWidth();
                    var divInnerWidth = _div.width();
                    var realWidth = innerTextWidth + (tabOuterwidth - divInnerWidth);

                    totalWidth += realWidth;
                });

                if (totalWidth > tabWrapperWidth) {
                    itemWidth = tabWrapperWidth / resArr[0];

                    tabItems.width(itemWidth);
                } else {
                    tabItems.width('auto');
                }
            }
            /*
            
             */
        });
    }
  }

})();

