(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('turnsService', turnsServiceFunc);

  /** @ngInject */
  function turnsServiceFunc($window, apiHost, $http, $log, $q, toastr, security) {
    var service = {
        saveSchedule: saveSchedule,
        newSchedule: newSchedule,
        getSchedule: getSchedule,
        copySchedule : copySchedule
    };

    return service;

    /**
     * [del ]
     * @param  {[string]} seqId []
     * @return {[object]}       [promise]
     */

      function getSchedule(week) {
          var param = {};
          param.week = week;
          param.deptCode = security.getFromSession().role.deptCode;

          return $http.get(apiHost.schedule.getSchedule, {
              params: param
          })
          .then(complete);
      }

    /**
     * [save ]
     * @param  {[object]} dataObj [seqId: number; damageLevelName: string; flow: string]
     * @return {[object]}         [promise]
     */
    function saveSchedule(dataObj) {
        dataObj.deptCode = security.getFromSession().role.deptCode;
        dataObj.deptName = security.getFromSession().role.deptName;
      return $http.post(apiHost.schedule.saveSchedule, dataObj)
        .then(complete);
    }

    /**
     * [get ]
     * @param  {[object]} params []
     * @return {[object]}        [promise]
     */
    function newSchedule(week) {
      var param = {};

      param.deptCode = security.getFromSession().role.deptCode;
      param.week = week;

      return $http.get(apiHost.schedule.newSchedule, {
        params: param
      })
        .then(complete);
    }

      /**
       * [get ]
       * @param  {[object]} params []
       * @return {[object]}        [promise]
       */
      function copySchedule(week) {
          var param = {};

          param.deptCode = security.getFromSession().role.deptCode;
          param.outWeek = week;
          param.srcWeek = week-1;

          return $http.get(apiHost.schedule.copySchedule, {
              params: param
          })
          .then(complete);
      }

      function complete(response) {
          if (response.data.code === '0') {
              return response;
          }
      }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .controller('DepartmentManagerTurnsController', turnsControllerFunc);

  /** @ngInject */
  function turnsControllerFunc(turnsService,classesService, $log,toastr) {
    var vm = this;
    vm.saveTurns = saveTurns;
    vm.nextWeek = nextWeek;
    vm.backWeek = backWeek;
    vm.newTurns = newTurns;
    vm.copyWeek = copyWeek;

      initData();//

      function initData(){
          getSchedule();
      }

      classesService.getClassList().then(function(response){
          if(response){
              vm.classList = response.data.data.lst;
          }
      });

    function getSchedule(week){
        turnsService.getSchedule(week).then(function(response) {
            if (response) {
                vm.data = response.data.data;
                vm.lst = vm.data.dtls;
            }
        });
    }

    function saveTurns() {
      vm.data.dtls = vm.lst;

      turnsService.saveSchedule(vm.data).then(function(response) {
        if (response && response.data.code == 0) {
          toastr.success('', '');
            getSchedule(vm.data.weeks);
        }
      });
    }
        //
      function nextWeek(){
          if(vm.data.weeks){
              vm.data.weeks = vm.data.weeks + 1;
              getSchedule(vm.data.weeks);
          }
      }
      //
      function backWeek(){
          if(vm.data.weeks){
              vm.data.weeks = vm.data.weeks - 1;
              getSchedule(vm.data.weeks);
          }
      }
      //-
      function newTurns(){
          turnsService.newSchedule(vm.data.weeks).then(function(response) {
              if (response && response.data.code == 0) {
                  toastr.success('', '');
                  vm.data = response.data.data;
                  vm.lst = vm.data.dtls;
              }
          });
      }

      function copyWeek(){
          turnsService.copySchedule(vm.data.weeks).then(function(response){
                if (response && response.data.code == 0) {
                    toastr.success('', '');
                    vm.data = response.data.data;
                    vm.lst = vm.data.dtls;
                }
            });
      }
  }

})();

